.pages {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 18px;
    padding: 5px;
    margin-bottom: 15px;
}
.pageItem {
    display: inline-block;
    cursor: pointer;
    margin-right: 5px;
    border: 1px solid black;
    padding: 0 3px;
}
.pageItem.selectedPageItem {
    font-weight: 700;
    border: 2px solid black;
    color: #753341;
}

.arrowsButton img {
    width: 20px;
    height: 20px;
}